<script>
	import List from '../../componentes/list.vue'
	import Share from '../../componentes/share.vue'
	import uCharts from '@/uni_modules/qiun-data-charts/js_sdk/u-charts/u-charts.js';
	var uChartsInstance = {};

	

	export default { 
		data(){
			return{
				routeType: '',
				share_show:false, //分享
			    cWidth:"100%",

				cHeight: 1000,
				isStart: false, //未开始拍卖
				shadowStyle: {
					backgroundImage: "linear-gradient(-180deg,#F4F5F7 50%, rgba(255, 255, 255, .5) 80%)",
					paddingTop: "100px",
					marginTop: "-100px",
				},
				content: `Cream3D is a collection of random NFT generated and resides on the Ethereum Blockchain. We focus on…Cream3D is a collection of random NFT generated and resides on the Ethereum Blockchain. We focus on…Cream3D is a collection of random NFT generated and resides on the Ethereum Blockchain. We focus on…Cream3D is a collection of random NFT generated and resides on the Ethereum Blockchain. We focus on…`,
			}
		},

		created() {
			console.log(this.$route.query.type);
			this.routeType = this.$route.query.type;
		},
	
		components:{List,Share},
		methods:{
			shareClcik(){
				this.share_show=false
			},

			// 购买
			gopay(url) {
				this.$my.go(url || '/pages/goodsDetail/payAndOffer')
			},
			getServerData() {
				//模拟从服务器获取数据时的延时
				setTimeout(() => {
					//模拟服务器返回数据，如果数据格式和标准格式不同，需自行按下面的格式拼接
					let res = {
						categories: ["2016", "2017", "2018", "2019", "2020", "2021"],
						series: [{
								name: "成交量A",
								data: [35, 8, 25, 37, 4, 20]
							},
							{
								name: "成交量B",
								data: [70, 40, 65, 100, 44, 68]
							},
							{
								name: "成交量C",
								data: [100, 80, 95, 150, 112, 132]
							}
						]
					};
					this.drawCharts('nEmtvyIATremPTQuEyfQxQvcNabRwyzm', res);
				}, 500);
			},
			drawCharts(id, data) {
				const ctx = uni.createCanvasContext(id, this);
				uChartsInstance[id] = new uCharts({
					type: "line",
					context: ctx,
					width: this.cWidth,
					height: this.cHeight,
					categories: data.categories,
					series: data.series,
					animation: true,
					background: "#FFFFFF",
					color: ["#1890FF", "#91CB74", "#FAC858", "#EE6666", "#73C0DE", "#3CA272", "#FC8452", "#9A60B4",
						"#ea7ccc"
					],
					padding: [15, 10, 0, 15],
					legend: {},
					xAxis: {
						disableGrid: true
					},
					yAxis: {
						gridType: "dash",
						dashLength: 2
					},
					extra: {
						line: {
							type: "curve",
							width: 2
						}
					}
				});
			},
			tap(e) {
				uChartsInstance[e.target.id].touchLegend(e);
				uChartsInstance[e.target.id].showToolTip(e);
			},

			back() {
				uni.navigateBack()
			},
			load() {
				this.$refs.uReadMore.init();
			}
		}, 
		onReady() {
			//这里的 750 对应 css .charts 的 width
			this.cWidth = uni.upx2px(600);
			//这里的 500 对应 css .charts 的 height
			this.cHeight = uni.upx2px(400);
			this.getServerData();
		},
		onLoad() {
			// 模拟后端请求
			setTimeout(() => {
				// this.content = ``,
				// 请注意上方已在组件中添加ref-uReadMore
				this.$nextTick(() => {
					this.$refs.uReadMore.init();
				})
			}, 2000);
		}
	} 
</script>

